























































































































































































































































































































.timer {
  font-size: 12px;
  float: right;
  color: #999;
  text-align: right;
  width: 100%;
}
.entryLists {
  padding-bottom: 56px;
  .entry {
    height: 70px;
    display: flex;
    align-items: center;
    background: #fff;
    border-bottom: 1px solid #ededed;
    .entryContainer {
      width: 100%;
      .messagePreview {
        color: #999;
        text-align: initial;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        word-break: break-all;
      }
      .time {
        display: flex;
        justify-content: flex-end;
        width: 100%;
        font-size: 12px;
        color: #999;
      }
      .messageCount {
        padding: 0px 6px;
        border-radius: 50px;
        background: #f00;
        color: #fff;
        text-align: center;
        float: right;
        font-style: normal;
        font-size: 12px;
        margin-top: 4px;
      }
      .entryIcon {
        display: flex;
        justify-content: center;
        align-items: center;
        .diggIcon {
          border-radius: 100px;
          background-color: #fe8f90;
        }
        .messageAvatar {
          .avatar {
            width: 100%;
            border-radius: 50%;
          }
        }
        .commentIcon {
          background-color: rgb(89, 182, 215);
          border-radius: 100px;
        }
      }
    }
  }
}



