
























































































































































































.rankingContent {
  background-color: #fff;
  .ranking {
    border-bottom: 1px solid #ededed;
    padding: 8px 0;
    .rankRow {
      .rankAvatar {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        img {
          width: 100%;
          border-radius: 50%;
        }
        span {
          padding: 2px 0;
        }
      }
      .name {
        font-weight: 400;
        font-size: 16px;
        line-height: 100%;
        padding: 4px 0;
      }
      .intro {
        text-align: inherit;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        word-break: break-all;
        color: #999;
        padding: 2px;
      }
      .gray {
        color: #999;
        padding: 2px;
      }
      .diggCount {
        color: #59b6d7;
        padding: 0 4px;
      }
    }
    &:last-child {
      border-bottom: none;
    }
  }
}



