









































































































































































































































































































































































.comments {
  .comment {
    border-bottom: 1px #ededed solid;
    padding: 8px;
    background-color: #fff;
    display: block;
    &:last-child {
      border-bottom: none;
    }
    .commentContent {
      display: flex;
      align-items: flex-start;
    }
    .commentBox {
      li {
        margin-top: 8px;
      }
      .commentOperations {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        .commentOperation {
          margin: 0 8px;
          &:last-child {
            margin-right: 0;
          }
        }
      }
    }
  }
}
.sourceContent {
  background-color: #ededed;
  padding: 2px;
  position: relative;
  height: 100%;
  &:before {
    content: '';
    display: block;
    padding-top: 100%;
  }
  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    object-fit: cover;
    right: 0;
    bottom: 0;
    height: 100%;
  }
  .source {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    font-size: 10px;
    .content {
      padding: 4px;
      text-align: justify;
      overflow: hidden;
      text-overflow: ellipsis;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
      word-break: break-all;
      display: -webkit-box;
    }
  }
}



