  height: 45px;
  border-bottom: 1px #ddd solid;
  .ivu-row {
    width: 100%;
  }
  .ivu-row, .ivu-col {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    div {
      height: 100%;
    }
    a {
      display: flex;
      height: 100%;
      justify-content: center;
      align-items: center;
      padding: 5px 0;
    }
  }
}



