


































.content {
  padding: 10px 15px;
  background: #fff;
  border-radius: 10px;
  font-size: 14px;
  max-width: 70vw;
  text-align: center;
  display: flex;

}
.notice {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
  z-index: 10;
  margin: 0;
  background: rgba(0,0,0,.3);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}



