



















































































.component-list {
  display: flex;
  align-items: center;
  height: 50px;
  padding: 0 12px;
  background: #fff;
  border-bottom: 1px #e2e3e3 solid;
  .component-icon {
    display: inherit;
    width: 10vw;
    justify-content: flex-start;
  }
  .component-title {
    display: inherit;
    width: 82vw;
    justify-content: flex-start;
  }
  .component-entry {
    display: inherit;
    width: 8vw;
    justify-content: flex-end;
  }
}



