
























































































.detail {
  background-color: #fff;
  margin-bottom: 6px;
  padding: 10px 0 0;
  &:active, &:focus {
    background-color: #fff;
  }
}
.toolTop {
  margin-top: 5px;
  border-top: 1px #e2e2e2 solid;
  display: flex!important;
  align-items: center;
  height: 45px!important;
}
.usernameLine {
  margin-bottom: 6px;
}
.username {
  color: #333;
  font-size: 16px;
}
.content {
  font-size: 14px;
  color: #666;
  text-align: initial;
  overflow: hidden;
  text-overflow: ellipsis;  
  display: -webkit-box;  
  -webkit-line-clamp: 3;  
  -webkit-box-orient: vertical;
  word-break: break-all;
  margin-bottom: 8px;

}
.timer {
  text-align: right;
  color: #ccc;
  font-size: 12px;
}
.seat {
  visibility: hidden;
}



