
































.feedNavBar{
  font-size: 18px;
  height: 46px;
  line-height: 45px;
  border-bottom: 1px #ededed solid;
  background-color: #fff;
  position: fixed;
  width: 100%;
  z-index: 3;
  &:active {
    background-color: #fff;
  }
  .NavRow {
    height: 100%;
    justify-content: center;
    align-items: flex-end;
    .NavCol {
      position: relative;
    }
  }
}
.navLink {
  color: #999;
  display: block;
  text-align: center;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  bottom: -1px;
}



