  vertical-align: middle;
  font-weight: normal;
  font-style: normal;
}
.ivu-rate-disabled .ivu-rate-star:before,
.ivu-rate-disabled .ivu-rate-star-content:before {
  cursor: default;
}
.ivu-rate-disabled .ivu-rate-star:hover {
  transform: scale(1);
}
.ivu-rate-star {
  display: inline-block;
  margin: 0;
  padding: 0;
  margin-right: 8px;
  position: relative;
  font-family: 'Ionicons';
  transition: all 0.3s ease;
}
.ivu-rate-star:hover {
  transform: scale(1.1);
}
.ivu-rate-star:before,
.ivu-rate-star-content:before {
  color: #e9e9e9;
  cursor: pointer;
  content: "\F4B3";
  transition: all 0.2s ease-in-out;
  display: block;
}
.ivu-rate-star-content {
  position: absolute;
  left: 0;
  top: 0;
  width: 50%;
  height: 100%;
  overflow: hidden;
}
.ivu-rate-star-content:before {
  color: transparent;
}
.ivu-rate-star-half .ivu-rate-star-content:before,
.ivu-rate-star-full:before {
  color: #f5a623;
}
.ivu-rate-star-half:hover .ivu-rate-star-content:before,
.ivu-rate-star-full:hover:before {
  color: #f7b84f;
}
.ivu-rate-text {
  margin-left: 8px;
  vertical-align: middle;
  display: inline-block;
  font-size: 12px;
}
.ivu-upload input[type="file"] {
  display: none;
}
.ivu-upload-list {
  margin-top: 8px;
}
.ivu-upload-list-file {
  padding: 4px;
  color: #495060;
  border-radius: 4px;
  transition: background-color 0.2s ease-in-out;
  overflow: hidden;
  position: relative;
}
.ivu-upload-list-file > span {
  cursor: pointer;
  transition: color 0.2s ease-in-out;
}
.ivu-upload-list-file > span i {
  display: inline-block;
  width: 12px;
  height: 12px;
  color: #495060;
  text-align: center;
}
.ivu-upload-list-file:hover {
  background: #f3f3f3;
}
.ivu-upload-list-file:hover > span {
  color: #2d8cf0;
}
.ivu-upload-list-file:hover > span i {
  color: #495060;
}
.ivu-upload-list-file:hover .ivu-upload-list-remove {
  opacity: 1;
}
.ivu-upload-list-remove {
  opacity: 0;
  font-size: 18px;
  cursor: pointer;
  float: right;
  margin-right: 4px;
  color: #999;
  transition: all 0.2s ease;
}
.ivu-upload-list-remove:hover {
  color: #444;
}
.ivu-upload-drag {
  background: #fff;
  border: 1px dashed #dddee1;
  border-radius: 4px;
  text-align: center;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: border-color 0.2s ease;
}
.ivu-upload-drag:hover {
  border: 1px dashed #2d8cf0;
}
.ivu-upload-dragOver {
  border: 2px dashed #2d8cf0;
}
.ivu-tree ul {
  list-style: none;
  margin: 0;
  padding: 0;
  font-size: 12px;
}
.ivu-tree ul li {
  list-style: none;
  margin: 8px 0;
  padding: 0;
  white-space: nowrap;
  outline: none;
}
.ivu-tree li ul {
  margin: 0;
  padding: 0 0 0 18px;
}
.ivu-tree-title {
  display: inline-block;
  margin: 0;
  padding: 0 4px;
  border-radius: 3px;
  cursor: pointer;
  vertical-align: top;
  color: #495060;
  transition: all 0.2s ease-in-out;
}
.ivu-tree-title:hover {
  background-color: #eaf4fe;
}
.ivu-tree-title-selected,
.ivu-tree-title-selected:hover {
  background-color: #d5e8fc;
}
.ivu-tree-arrow {
  cursor: pointer;
}
.ivu-tree-arrow i {
  transition: all 0.2s ease-in-out;
}
.ivu-tree-arrow-open i {
  transform: rotate(90deg);
}
.ivu-tree-arrow-hidden {
  cursor: auto;
}
.ivu-tree-arrow-hidden i {
  display: none;
}
.ivu-tree-arrow-disabled {
  cursor: not-allowed;
}
html,
body {
  padding: 0;
  margin: 0;
  font-size: 14px;
  font-family: Helvetica, sans-serif;
  background-color: #f4f5f5;
  color: #333;
}
html:focus,
body:focus,
html:active,
body:active {
  background-color: #f4f5f5;
}
img[lazy="loading"],
img[lazy="error"] {
  width: 100%;
  height: 100%;
  background: #ededed;
}
ul,
li {
  padding: 0;
  margin: 0;
}
li {
  list-style-type: none;
}
li p {
  margin: 5px 0;
}
.primary-color {
  color: #59b6d7;
}
.gray-color {
  color: #999;
}
#spinner {
  position: fixed;
  top: 46px;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 8;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  width: 100%;
}
#spinner-parent {
  align-items: center;
  display: flex;
  justify-content: flex-start;
  width: 6vw;
  height: 6vw;
}
.spinner-double-bounce-bounce1,
.spinner-double-bounce-bounce2 {
  width: 6vw;
  height: 6vw;
  border-radius: 100px;
  opacity: 0.6;
  position: absolute;
  background-color: #59b6d7;
  -webkit-animation: mint-spinner-double-bounce 2s infinite ease-in-out;
  animation: mint-spinner-double-bounce 2s infinite ease-in-out;
}
.spinner-double-bounce-bounce2 {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}
@-webkit-keyframes spinner-double-bounce {
  0%,
  100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
@keyframes mint-spinner-double-bounce {
  0%,
  100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
