




























































































































































































































	.commonHeader {
		height: 46px;
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
	}
	.message {
		margin: 2vh 0;
	}
	.mymessage, .hemessage {
		display: flex;
		align-items: flex-start;
	}
	.hemessage {
		.msg-content {
			padding: 2vw;
		  	max-width: 60vw;
		  	background: #ededed;
		  	color: #333;
		  	border-radius: 0 10px 10px 10px;
		  	float: left;
		  	word-break: break-all;
		}
		.content {
			text-align: left;
			padding-right: 2vw;
		}
		.name {
			text-align: left;
		}
	}
	.mymessage {
		.msg-content {
			padding: 2vw;
		  max-width: 60vw;
		  background: #59b6d7;
		  color: #333;
		  border-radius: 10px 0px 10px 10px;
		  float: right;
		  word-break: break-all;
		}
		.content {
			text-align: right;
			padding-left: 2vw;
		}
		.name {
			text-align: right;
		}
	}
	.content {
		width: 84vw;
    display: inline-block;
    .name{
			color: #999;
	    font-weight: normal;
	    line-height: 1.8;
		}
	}
	.avatar {
		padding: 0px 2vw;
	  	width: 16vw;
	  	border-radius: 50%;
	  	height: 12vw;
	}
	.sendBox {
		position: fixed;
		bottom: 0;
		left: 0;
		right: 0;
		display: flex;
		align-items: center;
		justify-content: center;
		min-height: 7vh;
		max-height: 30vh;
		padding: 1vh 0;
		background: #fff;

	}
	.messageList {
		padding-top: 9vh;
		padding-bottom: 7vh;
	}



