

































































































.detail {
  background-color: #fff;
  margin-bottom: 6px;
  padding: 16px 0 0;
  &:active, &:focus {
    background-color: #fff;
  }
  .userFeed {
    display: block;
    margin-bottom: 8px;
  }
}
.toolTop {
  margin-top: 8px;
  border-top: 1px #e2e3e3 solid;
  display: flex!important;
  align-items: center;
}
.usernameLine {
  margin-bottom: 6px;
}
.username {
  color: #333;
  font-size: 16px;
}
.content {
  font-size: 14px;
  color: #666;
  text-align: justify;
  overflow: hidden;
  text-overflow: ellipsis;  
  display: -webkit-box;  
  -webkit-line-clamp: 3;  
  -webkit-box-orient: vertical;
  word-break: break-all;
  margin-bottom: 8px;
}
.timer {
  text-align: right;
  color: #ccc;
  font-size: 12px;
}



