







































































































































































































































































































































.tool {
  display: flex;
  align-items: center;
  .toolItem {
    width: 30vw;
    display: flex; 
    align-items: center;
    .count {
      padding-left: 4px;
      color: #999;
      font-style: normal
    }
    &:last-child {
      width: 10vw;
      justify-content: flex-end;
    }
  }
}
.comment {
  li {
    margin-top: 8px;
  }
  .commentOperations {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    .commentOperation {
      margin: 0 8px;
      &:last-child {
        margin-right: 0;
      }
    }
  }
}
.userName {
  font-size: 13px;
  color: #333;
  border-bottom: none!important;
}
.commentContent{
  font-size: 14px;
  color: #999;
}



