































































































































.detail {
  background-color: #fff;
  margin-bottom: 6px;
  padding: 10px 0 0;
  &:active, &:focus {
    background-color: #fff;
  }
  .detailAvatar {
    padding: 0 2vw; 
    width: 14vw; 
    height: 10vw; 
    border-radius: 100%;
  }
  .detailTools {
    padding-left: 16vw; 
    width: 100vw; 
    padding-right: 2vw; 
    border-top: 1px #ededed solid; 
    padding-top: 3vw; 
    padding-bottom: 3vw;
  }
  .detailContentBefore {
    display: flex;
    align-items: center;
    justify-content: space-between;
    .detailUsername {
      color: #333;
      font-size: 16px;
    }
    .detailTimer {
      text-align: right;
      color: #ccc;
      font-size: 12px;
    }
  }
  .detailFeedContent {
    width: 80vw;
    .detailContent {
      font-size: 14px;
      color: #666;
      text-align: initial;
      overflow: hidden;
      text-overflow: ellipsis;  
      display: -webkit-box;  
      -webkit-line-clamp: 3;  
      -webkit-box-orient: vertical;
      word-break: break-all;
      margin: 4px 0 12px 0;
    }
  }
}



