  height: 46px;
  border-bottom: 1px #ddd solid;
  .ivu-row {
    width: 100%;
  }
  .ivu-row, .ivu-col {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    div {
      height: 100%;
    }
  }
}
.canvasAvatar {
  height: 100%;
  width: 100%;
}
.avatarOp {
  background: #fff;
}
span.operate {
  font-size: 16px;
}
span.operate.avatarDone{
  color: #59b6d7;
}
span.active {
  color: #59b6d7;
}
span.disabled {
  color: #999;
}
.userProfile-background-color {
  background-color: #fff;
}
.sexDefaultText {
  color: #999;
}



