
























































.channelfeeds {
  .channelInfo {
    position: relative;
    display: flex;
    justify-content: center;
    z-index: 9;
    .back {
      position: absolute;
      top: 12px;
      left: 12px;
      right: 0;
      z-index: 10;
    }
    .info {
      height: 90vw;
      width: 100vw;
      z-index: 8;
      overflow: hidden;
      background-color: #fff;
      img.cover {
        width: 100vw;
        height: 90vw;
        left: 0;
        top: 0; 
        right: 0;
        filter:blur(50px)
      }
      .detailInfo {
        width: 100vw;
        height: 90vw;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        color: #fff;
        img {
          object-fit: cover;
          width: 30vw;
          height: 30vw;
          border: 2px solid #fff;
        }
        h4 {
          padding: 4vw 0;

        }
        p {
          padding-bottom: 4vw;
        }
        section {
          i {
            font-style: normal;
            padding-right: 2vw;
          }
        }
      }
    }
  }
  .feedsLists {
    .mint-loadmore {
      overflow: visible;
    }
  }
  .plusIcon {
    position: fixed;
    bottom: 2vh;
    left: 0;
    right: 0;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    span {
      border-radius: 100px;
      background-color: #59b6d7;
      border: 2px solid #fff;
      padding: 10px;
      box-shadow: 0 0 5px #59b6d7;
    }
  }
}



