  background-color: #59b6d7;
  padding: 0;
}
.router-link {
  color: #999;
  text-align: center;
  svg {
    margin: 0 auto;
  }
}
.router-link-active {
  border-bottom: none;
  color: #59b6d7;
  svg {
    fill: #59b6d7;
    &:before {
      color: #59b6d7;
    }
  }
}



