








































































































































































































.profile {
  padding-bottom: 60px;
  .header {
    height: 46px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    font-weight: 400;
    background-color: #fff;
    border-bottom: 1px solid #ededed;
  }
  .avatar {
    width: 100%;
    border-radius: 50%;
  }
  .simpleInfo {
    padding-top: 16px;
    padding-bottom: 16px;
    background-color: #fff;
    border-bottom: 1px solid #ededed;
    .rowCenter {
      display: flex;
      align-items: center;
      .colCenter {
        display: flex;
        align-items: center;
      }
    }
  }
  .entry {
    margin-top: 12px;
    background-color: #fff;
    padding: 0 8px;
    .entryMenu {
      height: 50px;
      display: flex;
      align-items: center;
      border-bottom: 1px solid #ededed;
      &:last-child {
        border-bottom: none;
      }
      .menuText {
        padding-left: 0!important;
      }
    }
  }
  .followsContent {
    width: 100%;
    background: #fff;
    display: flex;
    padding: 16px 0;
    font-size: 16px;
    font-weight: 400;
    .contentCenter {
      text-align: center;
    }
    .contentCenterHalf {
      width: 50%;
      display: flex;
      justify-content: flex-end;
      float: left;
    }
    .newFollowsHalf {
      display: inline-block;
      border-radius: 100px;
      color: #fff;
      background: #f00;
      min-width: 16px;
      height: 16px;
      padding: 2px 4px;
      margin-left: 8px;
      text-align: center;
      vertical-align: inherit;
      font-size: 13px;
      line-height: 1;
    }
  }
  
  .rightIcon {
    display: flex!important;
    justify-content: flex-end!important;
  }
}



