


















































































































































































































































































.newsIndexContainer {
  padding-top: 46px;
  .newsLists{
    background-color: #fff;
    .new {
      padding: 12px;
      display: flex;
      align-items: flex-start;
      border-bottom: 1px solid #e2e3e3;
      .sourceTitle {
        width: 75vw;
        height: 18vw;
        padding-right: 8px;
        position: relative;
        h4 {
          color: #333;
          font-weight: 400;
          text-align: initial;
          overflow: hidden;
          text-overflow: ellipsis;  
          display: -webkit-box;  
          -webkit-line-clamp: 2;  
          -webkit-box-orient: vertical;
          word-break: break-all;
        }
        .sourceFrom {
          position: absolute;
          bottom: 0;
          line-height: 1;
          i {
            vertical-align: sub;
            color: #999;
            font-style: normal;
            font-size: 12px;
            &:last-child {
              margin-left: 16px;
            }
            &:first-child {
              margin-left: 0;
            }
          }
        }
      }
      .sourceImg {
        width: 25vw;
        height: 18vw;
        img {
          width: 100%;
          height: 18vw;
          object-fit: cover;
        }
      }
      &:last-child {
        border-bottom: none;
      }
    }
  }
}



