


































































































































































































































.FeedImages {
  width: 100%;
  img {
    width: 100%;
    object-fit: cover;
    display: flex;
  }
  .imagesRow {
    margin-bottom: 2px!important;
  }
}
.imgParent {
  overflow: hidden;
  &:before {
    content: "";
    display: block;
    padding-top: 100%;
  }
}
.showImgBox {
  position: relative;
  overflow: hidden;
  padding: 0 2px;
  left: -1px;
  margin-bottom: 2px;
  .perFeedImg {
    position: absolute;
    top: 0;
    left: 2px;
    object-fit: cover;
    height: 100%;
    width: 100%;
  }
  &:before {
    content: "";
    display: block;
    padding-top: 100%;
  }
}



