  .vux-header h1 {
    color: #000;
    height: 44px;
  }
  .weui-cells:after {
    height: 0;
    content: ""!important;
  }
  .weui-cell:before {
    right: 15px;
  }
  .vux-no-group-title {
    margin-top: 0;
  }
  .vux-header .vux-header-left a.vux-header-back{
    color: #59b6d7;
    padding-left: 0;
  }
  .left-arrow {
    display: none;
  }
  .action {
    color: #59b6d7;
    font-size: 16px;
  }
  .notAction {
    color: #999;
    font-size: 16px;
  }
}
.demo-upload-list{
  width: 22vw;
  height: 22vw;
  text-align: center;
  line-height: 22vw;
  overflow: hidden;
  background: #fff;
  position: relative;
  margin:.5vw;
}
.ivu-upload-drag {
  border-radius: 0;
  border: 1px #ededed solid;
  &:hover {
    border: 1px #ededed solid;
  }
}
.demo-upload-list img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.demo-upload-list-cover{
    display: none;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0,0,0,.6);
}
.demo-upload-list:hover .demo-upload-list-cover{
    display: block;
}
.demo-upload-list-cover i{
    color: #fff;
    font-size: 20px;
    cursor: pointer;
    margin: 0 2px;
}



