




























































































































































.commentCount {
  margin-bottom: .5em;
  font-size: 12px;
}
.commentRoot {
  position: fixed;
  // position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0;
  overflow: hidden;
  z-index: 7;
  background-color: #fff;
  .commentInput{
    // position: fixed;
    // bottom: 0;
    // left: 0;
    width: 100%;
    z-index: 7;
    // border-bottom: 1px #59b6d7 solid;
    border: none;
    padding: 10px 0;
    textarea {
      min-height: 34px!important;
      position: relative;
      bottom: 0;
      left: 0;
    }
  }
  .wrapper{
    background-color: rgba(0, 0, 0, .3);
    z-index: 6;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    position: absolute;
    overflow: auto;
    margin: 0;
  }
}



