



























































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































.comment {
  padding: 0 12px 12px 12px;
  background-color: #fff;
  li {
    margin-top: 8px;
  }
  .commentOperations {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    .commentOperation {
      margin: 0 8px;
      &:last-child {
        margin-right: 0;
      }
    }
  }
}
.followAction {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.perComment {
  align-items: flex-start!important;
  padding: 10px 0;
  .profileLink {
    color: #333;
  }
}
.wrapper{
  background-color: rgba(0, 0, 0, .3);
  z-index: 5;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  position: fixed;
  overflow: auto;
  margin: 0;
}
.sendComment {
  font-size: 14px;
  padding: 3px!important;
  background-color: #59b6d7;
  &[disabled] {
    background-color: #ccc!important;
    color: #fff!important;
  }
}
.commentContent{
  font-size: 14px;
  color: #999;
}
.operation {
  i {
    display: flex;
    display: -webkit-flex;
    -webkit-justify-content: center;
    -webkit-align-items: center;
    justify-content: center;
    align-items: center;
    font-style: normal;
  }
}



