
















































.channelIndex {
  position: relative;
  .ivu-tabs-bar {
    margin-bottom: 0;
    background: #fff;
    border-bottom: 1px #e2e3e3 solid;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 3;
    .ivu-tabs-nav-scroll {
      display: flex;
      justify-content: center;
      height: 46px;
      line-height: 45px;
      .ivu-tabs-nav {
        height: 100%;
        .ivu-tabs-tab{
          line-height: 2;
          font-size: 16px;
          &.ivu-tabs-tab-active {
            color: #333;
          }
        }
      }
    }
  }
  .ivu-tabs-tabpane {
    height: 100vh;
    padding-top: 46px;
    .channelCateParent {
      background-color: #fff;
      .channelItem {
        display: flex;
        align-items: center;
        justify-content: space-around;
        padding: 12px 0;
        border-bottom: 1px solid #e2e3e3;
        &:last-child {
          margin-bottom: 0;
          border-bottom: none;
        }
        .channelCover {
          height: 18vw;
          width: 25vw;
          padding: 0 12px;
          position: relative;
          overflow: hidden;
          padding: 0 12px;
          &:before {
            content: "";
            display: block;
            padding-top: 100%;
          }
          img {
            object-fit: cover;
            width: calc(~'100% - 24px');
            position: absolute;
            top: 0;
            left: 12px;
            right: 12px;
            bottom: 0;
          }
        }
        .channelIntro {
          width: 50vw;
          height: 18vw;
          position: relative;
          section {
            position: absolute;
            bottom: 0;
            i {
              color: #59b6d7;
              font-style: normal;
              margin-right: 12px;
            }
          }
        }
        .channelOperation {
          width: 25vw;
          display: flex;
          justify-content: flex-end;
          padding: 0 12px;
          .ivu-btn-ghost {
            display: flex;
            align-items: center;
            .ivu-icon-ios-plus-empty:before {
              color: #59b6d7;
              font-size: 24px;
            }
            .ivu-icon-ios-checkmark-empty:before {
              color: #999;
              font-size: 24px;
            }
          }
        }
      }
    }
  }
}



